let num = 60
export default {
    data(){
        return {
            tel:'',
            code:'',
            pwd:'',
            newpwd:'', //确认密码
            coun:'获取验证码'
        }
    },
    methods: {
        confirmRest(){
            if(this.tel.length < 11) {
                this.$toast('请输入正确的手机号')
                return false
            }
            if(this.code == '') {
                this.$toast('请输入验证码')
                return false
            }
            if(this.pwd == '') {
                this.$toast('请输入密码')
                return false
            }
            if(this.newpwd == '') {
                this.$toast('请输入确认密码')
                return false
            }
            if(this.newpwd != this.pwd) {
                this.$toast('两次输入的密码不一致')
                return false
            }
            this.$loading()
            let data = {
                phone:this.tel,
                phonecode:this.code,
                password:this.pwd,
                confirm_password:this.newpwd
            }
            this.$http.getfindpwd(data).then(res=>{
                if(res.status == 200) {
                    this.$toast(res.mess)
                    setTimeout(()=>{
                        this.getback()
                    },1000)
                }
            })
        },
        getcode(){
            // // 找回密码
            if(this.tel.length < 11) {
                this.$toast('请输入正确的手机号')
                return false
            }
            this.$loading()
            this.$http.getfindpwdcode({phone:this.tel}).then(res=>{
                if(res.status == 200) {
                    this.$toast(res.mess)
                    var timeId =setInterval( res=>{
                        --num
                        if(num == 0) {
                            this.coun = '重新获取'
                            clearInterval(timeId)
                        return false
                        }
                        this.coun = num + 's'
                    } ,1000)
                }
            })
        }
    },
}
